.register-btn {
    backdrop-filter: blur(5px);
    background: rgba(15, 15, 15, 0.4);
    border: 1px solid rgba(255, 255, 255, 1);
    box-shadow: 0 0 3.2px rgba(255, 255, 255, 1);
    border-radius: 8px;
    -webkit-backdrop-filter: blur(5px);
}
.register-btn.red:hover {
    background: rgba(255, 0, 0, 0.2);
    border: 1px solid rgba(255, 0, 0, 1);
    box-shadow: 0 0 3.2px rgba(255, 0, 0, 1);
    border-radius: 8px;
    animation: redOn 0.25s ease-in;
    animation-fill-mode: forwards;
}

.register-btn.blue:hover {
    background: rgba(0, 255, 235, 0.2);
    border: 1px solid rgba(0, 255, 230, 1);
    box-shadow: 0 0 3.2px rgba(0, 255, 230, 1);
    border-radius: 8px;
    animation: blueOn 0.25s ease-in;
    animation-fill-mode: forwards;
}

@keyframes blueOn {
    0% {
        background: rgba(0, 255, 235, 0.2);
        border: 1px solid rgba(0, 255, 230, 1);
        box-shadow: 0 0 3.2px rgba(0, 255, 230, 1);
        border-radius: 8px;
    }
    100% {
        background: rgba(15, 15, 15, 0.4);
        border: 1px solid rgba(255, 255, 255, 1);
        box-shadow: 0 0 3.2px rgba(255, 255, 255, 1);
        border-radius: 8px;
    }
}
@keyframes redOn {
    0% {
        background: rgba(255, 0, 0, 0.2);
        border: 1px solid rgba(255, 0, 0, 1);
        box-shadow: 0 0 3.2px rgba(255, 0, 0, 1);
        border-radius: 8px;
    }
    100% {
        background: rgba(15, 15, 15, 0.4);
        border: 1px solid rgba(255, 255, 255, 1);
        box-shadow: 0 0 3.2px rgba(255, 255, 255, 1);
        border-radius: 8px;
    }
}
